import { Injectable } from '@angular/core';
import { AngularFireStorage } from 'angularfire2/storage';
import { Observable } from 'rxjs';
import { AngularFireDatabase } from 'angularfire2/database';
import { Chapter } from './model/chapter';
import { DatabaseService } from './database.service';
import { mapToMapExpression } from '@angular/compiler/src/render3/util';

@Injectable({
  providedIn: 'root'
})
export class SlideshowService {

  chaptersMap: Map<Number, Chapter>;
  currentPage: number;
  pageMax: number;
  pageMinChapter: number;
  pageMaxChapter: number;
  currentChapter: number;
  currentChapterTitle: String;
  imageUrl: Observable<string | null>;
  nextImageUrl: Observable<string | null>;

  constructor(private afStorage: AngularFireStorage, private databaseService: DatabaseService) {
    this.currentPage = 1;
    this.currentChapter = 1;
    this.pageMax = 0;
    this.chaptersMap = new Map<Number, Chapter>();
    this.setImageUrl();
    this.retrieveData();
  }




  retrieveData() {
    this.databaseService.getChapters().valueChanges()   // returns observable
      .subscribe(list => {

        this.iterateChapters(list);
        this.setChapterTitle();
      });
  }


  public iterateChapters(chaptersList: Chapter[]) {
    for (let chapter of chaptersList) {
      this.chaptersMap.set(chapter.number, chapter);
      if (chapter.pageMax > this.pageMax) {
        this.pageMax = chapter.pageMax;
      }
    }
  }

  public setChapterTitle() {
    if (this.chaptersMap && this.chaptersMap.has(this.currentChapter)) {
      this.currentChapterTitle = this.chaptersMap.get(this.currentChapter).title;
      this.pageMinChapter = this.chaptersMap.get(this.currentChapter).pageMin;
      this.pageMaxChapter = this.chaptersMap.get(this.currentChapter).pageMax;
    }
  }

  public nextPage() {
    this.currentPage = this.currentPage + 1;
    this.computePage();
  }

  public previousPage() {
    this.currentPage = this.currentPage - 1;
    this.computePage();
  }

  computePage() {
    this.checkChapter();
    this.setImageUrl();
  }

  public getChaptersOrder() {
    let chaptersSet = new Set();
    for (var counter: number = 1; counter <= this.chaptersMap.size; counter++) {
      chaptersSet.add(this.chaptersMap.get(counter));
    }
    return chaptersSet;
  }


  public gotoPage(page: number) {
    if (page <= 0) { this.currentPage = 1; }
    else {
      if (page > this.pageMax) { this.currentPage = this.pageMax; } else {
        this.currentPage = page;
      }
    }
    this.computePage();
  }

  public gotoChapter(chapter: number) {
    this.currentPage = this.chaptersMap.get(chapter).pageMin;
    this.computePage();
  }

  checkChapter() {
    if (this.currentPage > this.pageMaxChapter) {
      this.currentChapter = this.currentChapter + 1;
      this.setChapterTitle();
    }

    if (this.currentPage < this.pageMinChapter) {
      this.currentChapter = this.currentChapter - 1;
      this.setChapterTitle();
    }
  }

  public isLastPage() {
    return this.currentPage == this.pageMax;
  }
  setImageUrl() {
    const ref = this.afStorage.ref(this.currentPage + '.jpg');
    this.imageUrl = ref.getDownloadURL();
  }

}
